import {
  uniIcons,
  uniNavBar
} from '@dcloudio/uni-ui'
export default {
  components: {
    uniIcons,
    uniNavBar
  },
  onLoad() {
    // console.log("-----index.page-----")
    console.log(this.list)
    this.query()
  },
  data() {
    return {
      // showPopup: false,
      showMoreIndex: null,
      page: 1,
      count: 20,
      list: []
    }
  },    
  onPullDownRefresh() {
    this.page += this.page
    this.query('pull')
  },
  onReachBottom() {
    // console.log(111)
    this.page += this.page
    this.query('bottom')
  },
  onPageScroll(e) {
    // console.log(e)
  },
  methods: {
    query(flag) {
      uni.request({
        url: 'https://api.apiopen.top/getJoke?page=' + this.page + '&count=' + this.count + '&type=video',
        method: 'GET', // 必须大写
        success: (res) => {
          console.log(res.data)
          if(!flag) {
            this.list = res.data.result
          }else {
            if(flag === 'pull') {
              this.list.unshift(...res.data.result)
            }
            if(flag === 'bottom') {
              this.list.push(...res.data.result)
            }
          }            
        },
        complete: () => {
          uni.stopPullDownRefresh();
        }
      })
    },
    clickView() {
      if (this.showMoreIndex !== null) {
        this.list[this.showMoreIndex].show = false
        this.showMoreIndex = null
      }
    },
    goSearch() {
      uni.navigateTo({
        url: '../search/search'
      })
    },
    clickMore(item, index) {
      // console.log(item.show)
      if (this.showMoreIndex === null) {
        this.$set(item, 'show', true)
        this.showMoreIndex = index
      } else {
        this.list[this.showMoreIndex].show = false
        this.showMoreIndex = null
      }
    }
  }
}
